@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
:root {
  /* Color Palette Selection */
  --navy: #2B2D42;
  --navy-lighter: hsl(235, 21%, 26%);
  --teal: #41E2BA;
  --white: #F7F7F9;
  --white-darker: hsl(240, 14%, 90%);
  --gray: hsl(0, 0%, 50%);
  --gray-darker: hsl(0, 0%, 35%);
  --gray-darkest: hsl(0, 0%, 20%);
  --yellow: #F7E733;
  --orange: #EE6C4D;
  --max-content-width: 900px;
}

.background-white {
  background-color: #F7F7F9;
  background-color: var(--white);
}

.background-teal {
  background-color: #41E2BA;
  background-color: var(--teal);
}

.background-navy {
  background-color: #2B2D42;
  background-color: var(--navy);
}

.color-white {
  color: #F7F7F9;
  color: var(--white);
}

.color-teal {
  color: #41E2BA;
  color: var(--teal);
}

.color-navy {
  color: #2B2D42;
  color: var(--navy);
}

html {
  scroll-behavior: smooth;
  font-family: "Raleway";
  /* color: var(--white); */
  background-color: #F7F7F9;
  background-color: var(--white);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.flex.row {
  flex-direction: row;
}
.navbar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background: var(--navy);
  font-size: 20px;
  color: var(--white);
  padding: 0.5em 0;
  position: -webkit-sticky;
  position: sticky;
  z-index: 100;
  top: 0;
  border-bottom: 2px solid var(--teal);
}

.navbar-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  max-width: var(--max-content-width);
}

.navbaar-links {
  display: flex;
  flex-direction: row;
}

.navbar-link {
  display: inline;
  margin: 0 20px;
}

footer {
  background: var(--navy);
  height: 100px;
}

.footnote {
  color: rgba(247,247,249, 0.35);
  text-align: center;
}
.highlight {
  color: var(--teal);
}

.banner {
  display:flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh);;
  margin:0;
  padding:0;
  background-color: var(--navy);
  color: var(--white);
  font-weight: normal;
  font-size: 50px;
  font-family: "Raleway";
}

.banner-bg-canvas {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.banner-text {
  z-index: 1;
  pointer-events: none;
}
.home-content {
  width: 100%;
  max-width: 1600px;
  min-height: 100vh;
  margin: 0px auto;
}

.home-content section {
  margin: 0px auto;
}

.home-content section:nth-child(odd) {
  background-color: var(--white-darker);
}

.about-me-section {
  display: flex;
  justify-content: center;
}

.about-me-content {
  display: grid;
  grid-template-columns: 4fr 2fr;
  padding-bottom: 40px;
  grid-gap: 50px;
  gap: 50px;
}

.about-me-description {
  text-align: justify;
}

.about-me-content {
  max-width: var(--max-content-width);
}


.about-me-profile-container {
  height: 250px;
  width: 250px;
  position: relative;
  box-sizing: border-box;
  z-index: 0;
}

.about-me-profile-inner {
  width: 100%;
  height: 100%;
  position: absolute;
  border: 4px solid var(--teal);
  border-radius: 5px;
  top: 20px;
  left: 20px;
  z-index: -1;
  box-sizing: border-box;
}

.profile-pic {
  z-index: 0;
  border-radius: 5px;
  width: 100%;
}
.header-underline {
    width: 100px;
    height: 3px;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 100px;
    padding: 0;
    background: var(--navy);
}
.experience-section {
    display: flex;
    justify-content: center;
    padding: 20px 0px;
}

.experience-content {
    width: 100%;
    max-width: var(--max-content-width);
    min-height: 400px;
}

.experience-viewer {
    display:  grid;
    grid-template-columns: 2fr 5fr;
    width: var(--max-content-width);
}

.tabpanel {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
}

/* experience panel descriptions */

.experience-title {
    margin: 0px;
}

.experience-range {
    color: rgb(117, 117, 117);
    margin: 0;
}

.experience-description {
    color: rgb(107, 107, 107);
    font-size: 14px;
}
.project-card {
    display: inline;
    width: 200px;
    padding: 1em;
    border: 2px solid var(--navy);
    display: flex;
    flex-direction: column;
}

.projects-content {
    display: flex;
    justify-content: center;
}

.projects-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.project-card-top {
    display: flex;
    justify-content: space-between;
}

.project-card-tech-list {
    padding: 0;
    color: var(--gray);
    display: flex;
    justify-content: space-between;
    list-style-type: none;
}


